<script>
  import moment from 'moment'
  import qs from 'qs'
  import * as carMsg from '../../api/carMsg'
  export default {
    methods: {
      updateCL(val){
        var self=this;
        var url=self.baseUrl+"vehicleInfo/update";
        var plateColor;
        console.log(self.editCLForm.ownerGender);
        console.log(val.plateColor);
        if(val.plateColor>0){
          plateColor=self.plateColorOptions[val.plateColor-1].label;
        }else if(val.plateColor){
          plateColor=val.plateColor
        }else{
          self.$message({
            message:"请选择车牌颜色!",
            type:"info"
          })
          return;
        }
        var params={
          "id":self.editCarId,
          "parkingId":val.parkingId,
          "vehicleClassifyId":val.vehicleClassifyId,
          "plateNo":val.plateNo,
          "plateColorNo":val.plateColor,
          "plateColor":plateColor,
          "vehicleColor":val.vehicleColor,
          "vehicleModel":val.vehicleModel,
          "vehicleOwner":val.vehicleOwner,
          "ownerIdNo":val.ownerIdNo,
          "ownerGender":val.ownerGender,
          "ownerBirth":self.momentTime2(val.ownerBirth),
          "ownerPhone":val.ownerPhone,
          "ownerAddress":val.ownerAddress,
          "rfidNo":val.rfidNo
        };
        self.$axios.post(url,params).then(function(res){
          if(res.data.status=='0'){
            self.$message({
              message:res.data.message,
              type:"success"
            })
            self.editCarDialog=false;
            self.sendCarType(self.carTypeForm.vehicleTypeId);
          }else{
            self.$message({
              message:res.data.message,
              type:"info"
            })
          }
        }).catch(err=>console.log(err))


      },
      cancelCL2(){
        this.editCarDialog=false;
      },
      //查询分组下车辆
      getFZCL(){
        var self=this;

      },
      //删除车辆
      delCarMsg(index,row){
        var self=this;
        var url=self.baseUrl+"vehicleInfo/deleteVehicle?id="+row.id;
        self.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          self.$axios.get(url).then(function(res){
          if(res.data.status=='0'){
            self.$message({
              message:res.data.message,
              type:"success"
            })
            console.log(self.carTypeForm.vehicleTypeId);
            if(self.carTypeForm.vehicleTypeId){
              self.sendCarType(self.carTypeForm.vehicleTypeId);
            }
          }else{
            self.$message({
              message:res.data.message,
              type:"info"
            })
          }
        })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });

      },
      cancelCL(){
        var self=this;
        self.xzclDialog=false;
      },
      addCL(val){
        var self=this;
        console.log(val)
        console.log(self.plateColorOptions[val.plateColor-1].label)
        var url=self.baseUrl+"vehicleInfo/add";
        var params={
          "parkingId":val.parkingId,
          "vehicleClassifyId":val.vehicleTypeId,
          "plateNo":val.plateNo,
          "plateColorNo":val.plateColor,//车牌颜色
          "plateColor":self.plateColorOptions[val.plateColor-1].label,
          "vehicleColor":val.vehicleColor,
          "vehicleModel":val.vehicleModel,
          "vehicleOwner":val.vehicleOwner,
          "ownerIdNo":val.ownerIdNo,
          "ownerGender":val.ownerGender,
          "ownerBirth":self.momentTime2(val.ownerBirth),
          "ownerPhone":val.ownerPhone,
          "ownerAddress":val.ownerAddress,
          "rfidNo":val.rfidNo
        }
        self.$axios.post(url,params).then(function(res){
          console.log('新增后的回显')
          console.log(res.data)
          if(res.data.status=='0'){
            self.$message({
              message:res.data.message,
              type:"success"
            })
            self.xzclDialog=false;
            self.sendCarType(carTypeForm.vehicleTypeId);
          }else{
            self.$message({
              message:res.data.message,
              type:"info"
            })
          }
        }).catch(err=>{console.log(err)})
      },
      // 车牌颜色
		getCarColor(){
			var self=this;
      var url=self.baseUrl+"vehicleInfo/plateColorList"
      carMsg.plateColorList().then(res=>{
        if(res.data.status=='0'){
					for(var i=0;i<res.data.data.length;i++){
						self.plateColorOptions[i]={
							id:res.data.data[i].id,
							label:res.data.data[i].plateColor,
							value:res.data.data[i].plateColorNo,
						}
					}
        }
        self.$forceUpdate();
      }).catch(function(err){
				console.log(err);
			})
		},
      //新增车辆
      addCarMsg(){
        var self=this;
        if(!Number(self.carTypeForm.vehicleTypeId)){
          self.$message({
            message:"请选择车辆分类",
            type:"info"
          })
          return;
        }
        self.xzclDialog=true;
      },
      //编辑车辆分类
      editCarMsg(index,row){
        var self=this;
        self.editCarId=row.id //传递id的值;
        console.log('编辑车辆分类')
        self.editCarDialog=true;
        //请求车辆信息回显接口;
        var url=self.baseUrl+"vehicleInfo/detail?id="+row.id;
        self.$axios.get(url).then(function(res){

          if(res.data.status=='0'){
            //车辆信息回显：
            self.editCLForm=res.data.data;
            self.editCLForm.ownerBirth=self.momentTime2(self.editCLForm.ownerBirth);
            self.editCLForm.ownerGender=String(self.editCLForm.ownerGender)
            console.log('缺少parkingId、分类Id')
            console.log(self.editCLForm)
          }else{
            self.$message({
              message:"请求失败，请重试!",
              type:"info"
            })
          }
        }).catch(err=>console.log(err))
      },
      //实时获取table
      sendCarType(val){
        if(!val){
          this.$message({
            message:"请选择车辆类型!",
            type:"info"
          })
        }
        var self=this;
        var url=self.baseUrl+"vehicleClassify/vehicleForClassify?vehicleClassifyId="+val;
        // var 
        //分页查询;
        self.$axios.post(url).then(function(res){
          console.log('实时table信息')
          console.log(res.data.data.dataList)
          self.dataList=res.data.data.dataList;
        })

      },
      // 车辆类型
		getCarType(){
      var self=this;
      carMsg.initVehicleClassify().then(res=>{
        if(res.data.status=='0'){
					for(var i=0;i<res.data.data.length;i++){
						self.vehicleTypeOptions[i]={
							id:res.data.data[i].id,
							label:res.data.data[i].vehicleClassifyName,
							value:res.data.data[i].id,
						}
					}
        }
        self.$forceUpdate();
      }).catch(function(err){
				console.log(err);
			})
		},
      //查询车辆分类
      searchTableMsg(val){
         var self=this;
         var url=self.baseUrl+"vehicleClassify/add";
         var params={
           "parkingId":self.addCarType.parkingId,
           "vehicleClassifyName":self.addCarType.carType
         }
        self.$axios.post(url,params).then(function(res){
          if(res.data.status=='0'){
            self.$message({
              message:res.data.message,
              type:"success"
            })
            self.addCarVisible=false;
          }else{
            self.$message({
              message:res.data.message,
              type:"info"
            })
          }

        }).catch(err=>{console.log(err)})
         
      },
      getCarMsg2(){
        var self=this;
        var params={
          "parkingId":self.parkingNameVal
        };
        self.axiosMsg(params);
      },
      // 取消按钮
      cancelEdit(){
        this.editVisible=false;
      },
      saveEdit(){
        var self=this;
        return;
        var url=self.baseUrl+"vehicleClassify/update";
        var params={
        }
        console.log("params")
        console.log(params)
        self.$axios.post(url,params).then(function(res){
          self.editVisible=false;
          if(res.data.status=='0'){
            self.$message({
              message:"保存成功!",
              type:"success" 
            })
            var params={}
            self.axiosMsg({});
          }else{
            self.$message({
              message:"保存失败",
              type:"info"
            })
          }
        }).catch(function(err){
          console.log(err);
        })

      },
      // 编辑按钮
      handleChangeMsg(index,row){
        var self=this;
        self.getCarType();
        self.editVisible=true;
        // var url=self.baseUrl+"vehicleClassify/detail?id="+row.id;
        self.carTypeForm=row;
        // self.$axios.get(url).then(function(res){
        //   self.vehicleTypeOptionsShow=true;
        //   console.log("分类回显的值")
        //   console.log(res.data.data)
        //   self.carTypeForm.vehicleTypeId=Number(res.data.data.id);

        // }).catch(err=>{console.log(err)})

      },
      // 取消按钮
      cancelEnter(){
        this.addCarVisible=false;
      },
      saveEnter(){
        var self=this;
        var url=self.baseUrl+"vehicleClassify/add";
        var params={
          "parkingId":self.addCarType.parkingId,
          "vehicleClassifyName":self.addCarType.carType,
        }
        self.$axios.post(url,params).then(function(res){
          if(res.data.status=='0'){
            self.$message({
              message:res.data.message,
              type:"success"
            })
            self.addCarVisible=false;
          }else{
            self.$message({
              message:res.data.message,
              type:"success"
            })
          }
        })
        // 调用查询接口
        self.getCarMsg2();
      },
      // 车型下拉列表
      getCarList(){
        var self=this;
        var url=self.baseUrl+'vehicleInfo/showVehicleTypeList';
        self.$axios.get(url).then(function(res){
          console.log('res')
          console.log(res.data.data)
          for(var i=0;i<res.data.data.length;i++){
            self.carTypeOptions[i]={
              id:res.data.data[i].id,
              label:res.data.data[i].vehicleType,
              value:res.data.data[i].id,
            }
          }
        }).catch(err=>console.log(err))
      },
      //驾驶证 车辆类别
      getjjcllx(){
        var self=this;
        var url=self.baseUrl+'vehicleInfo/initJszCllx';
        carMsg.initJszCllx().then(res=>{
          for(var i=0;i<res.data.data.length;i++){
            self.jjcllxOptions[i]={
              id:res.data.data[i].id,
              label:res.data.data[i].cllx,
              value:res.data.data[i].id,
            }
          }
        }).catch(err=>console.log(err))
      },
      getPlateColor(){
        var self=this;
      var url=self.baseUrl+"vehicleInfo/plateColorList"
      carMsg.plateColorList().then(res=>{
        if(res.data.status=='0'){
					for(var i=0;i<res.data.data.length;i++){
						self.plateColorOptions[i]={
							id:res.data.data[i].id,
							label:res.data.data[i].plateColor,
							value:res.data.data[i].plateColorNo,
						}
					}
        }
        self.$forceUpdate();
      }).catch(function(err){
				console.log(err);
			})
      },
      getHzrs(){
        var self=this;
        var url=self.baseUrl+'vehicleInfo/initJszHzrs';
        carMsg.initJszHzrs().then(res=>{
          for(var i=0;i<res.data.data.length;i++){
            self.hzrsOptions[i]={
              id:res.data.data[i].id,
              label:res.data.data[i].hzrs,
              value:res.data.data[i].id,
            }
          }
        }).catch(err=>console.log(err))
      },
      axiosMsg(params){
        var self=this;
        var url=self.baseUrl+"vehicleClassify/list"
        this.paginationShow=true; 
        carMsg.vehicleClassifyQuery(params).then(response=>{
          self.total=response.data.data.total
          self.tableCar=response.data.data.records;
          for(var i=0;i<self.tableCar.length;i++){
            self.tableCar[i].updateTime=self.momentTime(self.tableCar[i].updateTime);
            }
        }).catch(function(err){
          console.log(err);
        })
      },
      // 切换每页显示
      handleSizeChange(val){
        this.pageSize=val;
        console.log(`每页显示${val}条`)
        var self=this;
        var params={
          'pageSize':this.pageSize,
          "parkingId":self.parkingNameVal,
          "currentPage":this.currentPage
        }
        self.axiosMsg(params);
      },
      // 当前页
      currentChange(currentPage){
        var self=this;
        this.currentPage=currentPage;
        var params={
          'pageSize':this.pageSize,
          "parkingId":self.parkingNameVal,
          "currentPage":this.currentPage
        }
        self.axiosMsg(params);
      },
      momentTime(val){
          return moment(val).format("YYYY-MM-DD HH:mm:ss");
      },
      momentTime2(val){
          return moment(val).format("YYYY-MM-DD");
      },
      getCarMsg(){
        console.log("点击进入新增")
        var self=this;
        self.addCarType.parkingName='';
        self.addCarType.carType='';
        self.addCarVisible=true;
      },
      handleDetailMsg(index,row,scope){
        console.log(row);
        var self=this;
        var url=self.baseUrl+"vehicleClassify/delete?id="+row.id;
        self.$confirm("确认删除?","提示",{
          confirmButtonText:"确定",
          cancelButtonText:"取消",
          type:"warning"
        }).then(()=>{
          self.$axios.get(url).then(function(res){
          console.log(res.data)
          if(res.data.status=='0'){
            self.$message({
              message:"删除成功",
              type:"success"
            })
            self.axiosMsg({});
          }else{
            self.$message({
              message:res.data.message,
              type:"info"
            })
          }
        }).catch(function(err){
          console.log(err);
        })

        }).catch(()=>{
          self.$message({
            type:"info",
            message:"已取消删除"
          })
        })

        //获取图片信息
      },
      getParkName(){
        var self=this;  
         carMsg.parkingId(localStorage.getItem("userId")).then(res=>{
              if(res.data.status=='0'){
              for(var i=0;i<res.data.data.length;i++){
                self.parkingIdOptions[i]={
                  id:res.data.data[i].id,
                  label:res.data.data[i].parkingName,
                  value:res.data.data[i].id
                }
              }
            }
            self.$forceUpdate();
         }).catch(err=>{
           console.log(err);
         })
      },
    },
    data() {
      return {
        editCarId:'',
        editCLForm:{},
        parkingIdOptionsShow:false,
        plateColorOptionsShow:true,
        addCLForm:{},
        xzclDialog:false,
          vehicleTypeOptionsShow:true,
          vehicleTypeOptions:[],
          dataListShow:false,
          dataList:[],
          parkingIdOptionShow:true,
          inputCph:'',
          parkingNameVal:"",
          parkingIdOptions:[],
          paginationShow:false,
          tableShow:[],
          tableCar:[],
          total:0,
          currentPage:1,
          pageSize:10,
          dialogVisible:false,
          row:Object,
          imgRul2:'',
          zfVal:'',
          zfOptions:[
            {id:1,label:"已支付",value:"true"},
            {id:2,label:"未支付",value:"false"}
          ],
          tiemVal:'',
          editVisible:false,
          editCarDialog:false,
          carTypeForm:{},
          addCarVisible:false,
          addCarType:{},
          carTypeOptions:[],
          jjcllxOptions:[],
          parkingTypeOptions:[
            {id:0,value:0,label:"运营"},
            {id:1,value:1,label:"非运营"},
          ],
          hzrsOptions:[],
          plateColorOptions:[],

      }
    },
    mounted:function(){
      this.axiosMsg({});

      // this.getCarList();
      this.getjjcllx();
      this.getHzrs();
      this.getPlateColor();
      this.getParkName();
      this.getCarType();
      this.getCarColor();
    }
  }
</script>
<style scoped>
	.laeblStyle{
		width:220px;
  	text-align:right
	}
  .handle-input{
  width:140px;
  display: inline-block;
}	

.customWidth{
  /* width:10%; */
}
.fl{
  float:left;
}.fr{
  float:right;
}
.bd{
  height:14%;
  font-size:0.8em;
  line-height:1em;
  margin-bottom:10px;
}
.marL{
  margin-right:20%;
}
.width{
  width:30%
}
.width2{
  width:30%
}
.textLeft{
  text-align: left;
}
.handle-input{
  width:150px;
  /* display:inline-block */
}
</style>


